.transaction-history {
  width: 400px;
  margin: 0 auto 4rem;

  background-color: rgba(232, 228, 228, 0.5);
  box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;
}

.table-caption {
  margin-bottom: 1rem;

  font-size: 1.5rem;
  font-weight: 500;
  text-align: center;
  text-shadow: 1px 3px 12px rgba(150, 150, 150, 1);
}

th {
  background-color: rgba(175, 173, 173, 0.5);
  font-weight: normal;
}
td {
  padding: 0 0.5rem;
  font-size: 14px;
  font-weight: normal;
}

td:nth-child(3n + 2) {
  text-align: end;
}

tr:nth-child(even) {
  background-color: rgba(175, 173, 173, 0.2);
}

tr:hover {
  outline: 1px solid darkslateblue;
}
